import styled from "styled-components";
import { colores } from "./StylesApp";

const Section = styled.section`
  @import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;600;700;800&display=swap");
  display: flex;
  flex-direction: column;
  font-family: "Raleway", sans-serif; ;
`;

const Banner = styled.section`
  max-width: 100%;
  div {
    width: 100%;
    img {
      width: 100%;
      opacity: 0.4;
    }
  }
`;

const Content = styled.section`
  position: absolute;
  top: 10%;
  left: 6%;
  width: 50%;
  color: ${colores.darkblue};
  h1 {
    font-weight: 800;
    font-size: 500%;
    margin-bottom: 0;
  }
  p {
    font-size: 200%;
    font-weight: 600;
  }
  @media (max-width: 1200px) {
    top: 5%;
    min-width: 40%;
    h1 {
      font-weight: 700;
      font-size: 300%;
      margin-bottom: 0;
    }
    p {
      font-weight: 300;
      min-width: 30%;
    }
  }
  @media (max-width: 768px) {
    min-width: 40%;
    width: 90%;
    h1 {
      font-weight: 700;
      font-size: 200%;
    }
    p {
      font-weight: 300;
      font-size: 150%;
    }
  }
`;
const SecCont = styled.section`
  display: flex;
  justify-content: space-around;
  max-width: 80em;
  margin: 4em 2em;
  img {
    /* position: absolute; */
    padding-top: 5em;
    width: 30%;
    height: 25%;
    @media (max-width: 1024px) {
      width: 20%;
      height: 15%;
    }
    @media (max-width: 425px) {
      visibility: hidden;
    }
  }
  @media (max-width: 425px) {
  }
`;

const SecQueEs = styled.section`
  display: flex;
  flex-direction: column;
  max-width: 50em;
  padding: 0.5em 0.5em 0.5em 1em;
  p {
    @media (max-width: 768px) {
      width: 95%;
    }
  }
`;

const Ptitle = styled.p`
  font-weight: 800;
  font-size: 300%;
  color: ${colores.darkblue};
  @media (max-width: 425px) {
    font-size: 200%;
  }
`;
const P = styled.p`
  font-weight: 600;
  font-size: 200%;
  margin-right: 5em;
  color: ${colores.darkblue};
  span {
    color: ${colores.orange};
  }
  @media (max-width: 425px) {
    font-size: 150%;
  }
`;

const SecComienza = styled.section`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 3em 2em;
`;

const DivBtn = styled.div`
  display: flex;
  margin-top: 1rem;
`;

const ImgEmprendedor = styled.img`
  width: 100%;
  @media (max-width: 1024px) {
    width: 80%;
  }
`;
const ImgCiudadano = styled.img`
  width: 100%;
  @media (max-width: 1024px) {
    width: 80%;
  }
`;

const ImgBtn = styled.img`
  z-index: 100;
  position: relative;
  @media (max-width: 1024px) {
    width: 30%;
    height: 30%;
  }
`;

const ForoF = styled.section`
  height: 500px;
  font-size: medium;
  h1 {
    text-align: center;
    margin: 300px auto 0 auto;
  }
`;

export {
  SecQueEs,
  Section,
  Banner,
  Content,
  SecCont,
  Ptitle,
  P,
  SecComienza,
  DivBtn,
  ImgEmprendedor,
  ImgCiudadano,
  ImgBtn,
  ForoF,
};
